fundsFlowTypes: ['TYP_BUYER_GLOD_PAY', 'TYP_BUYER_GOLD_CHARGE']
}
},
set(val) {
return val
}
},
availableGold: {
get() {
let gold = 0
if (this.userCoin.availableGold) {
gold = parseFloat(this.userCoin.availableGold).toFixed(2)
}
return gold
},
