setInfo(data) {
let mylist = []
for (let item of data.fundsFlows) {
let obj = {
title: item.content,
time: item.gmtModify,
money: `${item.fundsFlowType === 'TYP_BUYER_GLOD_PAY' ? `-${item.pay.toFixed(2)}` : `+${item.income.toFixed(2)}`}`,
unit: '金币',
}
mylist.push(obj)
}
this.rechargeArr = [...this.rechargeArr, ...mylist]
this.$nextTick(() => {
this.$refs.scrollContent.refresh()
})
},

